/********************
  Buttons
*********************/

/* General - for all buttons */

$btn-height: 41px;
$btn-alt-height: 41px; // used for flat buttons (desktop) and generally for mobile height

$btn-border-width: 1px;
$btn-border-width-hover: 1px;
$btn-border-radius: 10px;
$btn-border-style: solid;

$btn-text-transform: none;
$btn-font-weight: 300;
$btn-font-size-mobile: 16px;
$btn-font-size-desktop: 18px;

$btn-padding-mobile-horizontal: 10px;
$btn-padding-mobile-vertical: 6px;
$btn-padding-mobile-horizontal-hover: 10px;
$btn-padding-mobile-vertical-hover: 6px;

$btn-padding-desktop-horizontal: 10px;
$btn-padding-desktop-vertical: 6px;
$btn-padding-desktop-horizontal-hover: 10px;
$btn-padding-desktop-vertical-hover: 6px;

/* Main Button */

$btn-main-color-bg: transparent;
$btn-main-color-txt: $black;
$btn-main-color-border: $black;

$btn-main-color-bg-hover: $black;
$btn-main-color-txt-hover: $white;
$btn-main-color-border-hover: $black;

/* Warning Button */

$btn-warn-color-bg: $color3;
$btn-warn-color-txt: $color2;
$btn-warn-color-border: $color3;

$btn-warn-color-bg-hover: transparent;
$btn-warn-color-txt-hover: $color3;
$btn-warn-color-border-hover: $color3;

/* Outline Button */

$btn-out-color-bg: transparent;
$btn-out-color-txt: $color2;
$btn-out-color-border: $color2;

$btn-out-color-bg-hover: transparent;
$btn-out-color-txt-hover: $color2;
$btn-out-color-border-hover: $color2;

/* Negative Outline Button */

$btn-neg-color-bg: transparent;
$btn-neg-color-txt: $white;
$btn-neg-color-border: $white;

$btn-neg-color-bg-hover: $white;
$btn-neg-color-txt-hover: $color2;
$btn-neg-color-border-hover: $white;

/* Negative Outline Button - Variant */

$btn-negvar-color-bg: $white;
$btn-negvar-color-txt: $color1;
$btn-negvar-color-border: $white;

/********************
  Arrow Links
*********************/

$al-color-txt: $colorText;
$al-color-border: $colorBorder;
$al-color-hover-icon: $color1;

/********************
  Navigational Buttons (.prev, .next)
*********************/

$navbtn-size: 46px;
$navbtn-border-radius: 46px;
$navbtn-color-bg: $color1;

$navbtn-arrow-color: $white;
$navbtn-arrow-size: 12px;
$navbtn-arrow-width: 2.5px;