/**
 * Styles for the tarteaucitron script
 * Set "useExternalCss": true when initiating tarteaucitron or
 */

@import 'variables';
@import 'mixins';
@import '../.build/node_modules/tarteaucitronjs/css/tarteaucitron';

html body {

  &.modal-open {
    @include media-breakpoint-up(md) {
      height: unset;
      overflow: unset;
    }
  }

  div#tarteaucitronRoot {

    button#tarteaucitronBack {
      background: transparent;
    }

    z-index: 9999999;

    & *, &:after, &:before {
      box-sizing: border-box !important;
    }

    &.tarteaucitronBeforeVisible {
      &:before {
        background: $black;
      }
    }

    /* Accept / Reject / Settings Dialog */

    div#tarteaucitronAlertBig {
      background: $color1;
      @include fluid(border-radius, $border-radius);
      @include fluid(margin-top, 0);
      @include fluid(margin-bottom, 0);
      @include fluidDiff(width, 350px, 60%);
      @include fluidDiff(max-width, 345px, 960px);
      box-shadow: 0 0 30px rgba($black, .2);
      overflow: hidden;

      @include fluidDiff(padding-top, 30px, 30px);
      @include fluidDiff(padding-right, 20px, 15px);
      @include fluidDiff(padding-bottom, 30px, 30px);
      @include fluidDiff(padding-left, 20px, 15px);

      * {
        font-family: $font-family-base;
      }

      /* Headline */

      &:before {
        font-weight: $font-weight-bold;
        @include fluid(font-size, 25px);
        @include fluid(line-height, 29px);
        @include fluid(margin-bottom, 20px);
        font-family: $font-family-headlines;
        display: block;
      }

      /* Text */
      #tarteaucitronDisclaimerAlert {
        @include fluidDiff(font-size, 15px, 16px);
        @include fluidDiff(line-height, 23px, 25px);
        margin: 0;
        @include fluid(margin-bottom, 30px);
        padding: 0;
      }

      /* Buttons */

      $tarte-btn-line-height-flat: $btn-alt-height - (2 * $btn-border-width);

      @mixin tarte-button {
        display: inline-block;

        background: $btn-neg-color-bg;
        color: $btn-neg-color-txt;
        border-color: $btn-neg-color-border;

        border-style: $btn-border-style;
        @include fluid(border-width, $btn-border-width);

        text-transform: $btn-text-transform;
        font-weight: $btn-font-weight;
        @include fluid(line-height, $tarte-btn-line-height-flat);
        @include fluidDiff(font-size, $btn-font-size-mobile, $btn-font-size-desktop);

        @include fluid(border-radius, $btn-border-radius);

        @include fluidDiff(padding-left, $btn-padding-mobile-horizontal, $btn-padding-desktop-horizontal);
        @include fluidDiff(padding-right, $btn-padding-mobile-horizontal, $btn-padding-desktop-horizontal);
        transition: box-shadow ease-in-out .2s,
        background ease-in-out .2s,
        border-color ease-in-out .2s,
        color ease-in-out .2s;

        &:hover {
          background: transparent;
          color: $color3;
          border-color: $color3;
        }
      }

      // Allow

      .tarteaucitronCTAButton {
        &.tarteaucitronAllow {
          background: $btn-negvar-color-bg;
          color: $btn-negvar-color-txt;
          border-color: $btn-negvar-color-border;

          &:hover {
            .tarteaucitronCheck:before {
              color: $color3;
            }
          }
        }

        &.tarteaucitronDeny {
          &:hover {
            .tarteaucitronCross:before {
              color: $color3;
            }
          }
        }
      }

      .tarteaucitronCheck:before {
        color: $btn-negvar-color-txt;
        transition: color ease-in-out .2s;
      }
      .tarteaucitronCross:before {
        transition: color ease-in-out .2s;
      }

      // All Buttons

      .tarteaucitronCTAButton,
      button#tarteaucitronCloseAlert {

        @include tarte-button;


        font-family: $font-family-base;
        font-size: pxToVwMobile($btn-font-size-mobile) !important;
        @include media-breakpoint-up(md) {
          font-size: $btn-font-size-desktop !important;
          line-height: $tarte-btn-line-height-flat !important;
        }

        font-weight: $font-weight-bold !important;


        @include media-breakpoint-up(md) {
          margin-bottom: 10px;
        }

      }

      // Link to DSGVO Page

      button#tarteaucitronPrivacyUrl {
        display: block;
        text-align: center;
        width: 100%;
        font-style: italic;
        @include fluidDiff(font-size, 15px, 16px);
        @include fluidDiff(line-height, 23px, 25px);
        @include fluid(padding-top, 10px);
        @media screen and (max-width: 900px) {
          margin: 20px auto 10px !important;
        }
      }

    }
  }

  /* Personalize */

  #tarteaucitron {

    // no borders

    .tarteaucitronBorder {
      border: none;
    }

    // upper area

    #tarteaucitronServices {
      box-shadow: none;

      .tarteaucitronMainLine,
      .tarteaucitronMainLine:hover {
        border-color: $color2;
        background: $color2;

        .tarteaucitronH1 {
          font-weight: $font-weight-bold;
          @include fluid(font-size, 25px);
          @include fluid(line-height, 29px);
          @include fluid(margin-bottom, 30px);
          font-family: $font-family-headlines !important;
        }

      }

      .tarteaucitronDetails,
      .tarteaucitronTitle button {
        background: $color2;
      }

    }

    // general background colors

    #tarteaucitronInfo {
      background: $color2;
    }

    // smaller info area

    #tarteaucitronInfo {
      margin: -10px auto 20px !important;
      border-top: none;
      border-bottom: none;
      padding-top: 0;
      padding-bottom: 0;
    }

    // close button

    #tarteaucitronClosePanel {
      background: $color2;
    }

  }

  #tarteaucitronAlertSmall {
    #tarteaucitronCookiesListContainer {
      #tarteaucitronCookiesList {
        .tarteaucitronTitle {
          background: $color2;
        }
      }
    }
  }


}
