/* General Border Radius */
$border-radius: 0; // e.g. for teasers

@import 'variables/fonts';
@import 'variables/colors';
@import 'variables/buttons';

@import 'variables/reboot';

/* Breakpoints */
$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        mdl: 880px,
        lg: 992px,
        lgl: 1024px,
        xl: 1200px,
        xll: 1385px,
        xlll: 1520px,
        xlplus: 1630px,
        teasersliderouter: 1850px,
        outer: 1920px,
        outerplus: 2120px
) !default;

$speaking-grid: (
        mobile: 0,
        mobile_mid: 576px,

        tablet: 768px,
        tablet_tiny: 880px,
        tablet_small: 992px,
        tablet_mid: 1096px,
        tablet_large: 1200px,

        desktop: 1385px,
        desktop_wide: 1630px,
        outer: 1920
) !default;




$maxWidthDesktop: map-get($grid-breakpoints, xl);
$maxWidthDesktopPlus: map-get($grid-breakpoints, xlplus);
$outerLimit: map-get($grid-breakpoints, outer);

/* Container Paddings & Margins */

$containerMarginMobile: 15px;
$containerMarginDesktop: 30px;


